<template>
  <img class="logo" alt="Vue logo" :src="logo_src">
</template>

<script>
export default {
  name: "AppLogo",
  computed: {
    logo_src() {
      return this.min ? require('../assets/logo-min.png') : require('../assets/logo.png')
    }
  },
  props: {
    min: { type: Boolean, default: false }
  }
}
</script>

<style scoped>
.logo {
  max-width: 38rem;
}
</style>