<template>
  <component
      :is="linkComponent"
      class="link"
      :[linkAttribute]="to || href"
      active-class="active"
  ><slot/></component>
</template>

<script>
export default {
  name: "HomeLinkItem",
  computed: {
    linkAttribute() {
      return this.to !== null ? 'to' : 'href'
    },
    linkComponent() {
      return this.to !== null ? 'router-link' : 'a'
    }
  },
  props: {
    to: {type: String, default: null},
    href: {type: String, default: null},
  }
}
</script>

<style scoped>
.link {
  color: white;
  font-family: Ubuntu, sans-serif;
  text-decoration: none;
  transition: color 150ms;
  margin-bottom: 2px;
}

.link:after {
  display: block;
  margin-bottom: -2px;
  content: '';
  width: 0;
  height: 2px;
  transition: width 150ms ease-in-out, background-color 150ms;
  background: white;
  float: right;
}

.link:hover:after {
  width: 100%;
  float: left;
}

.link:focus {
  color: #ccc;
}
.link:focus:after {
  background: #ccc;
  margin-right: auto;
  margin-left: auto;
  width: 20%;
  float: none;
}

.link.active {
  color: #00ff87;
}

.link.active:after {
  width: 0;
}

</style>